import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import TextFieldGroup from '../../common/TextFieldGroup'
import TextAreaFieldGroup from '../../common/TextAreaFieldGroup'
import SelectListGroup from '../../common/SelectListGroup'
import InputGroup from '../../common/InputGroup'
import PropTypes from 'prop-types'
import { createProfile } from '../../actions/profileActions'

class CreateProfile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            displaySocialInputs: false,
            handle: '',
            company: '',
            website: '',
            location: '',
            status: '',
            skills: '',
            githubusername: '',
            bio: '',
            wechat: '',
            QQ: '',
            tengxunkt: '',
            wangyikt: '',
            errors: {}
        }

        this.onSubmit = this.onSubmit.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            })
        }
    }

    onSubmit(e) {
        e.preventDefault();

        const profileData = {
            handle: this.state.handle,
            company: this.state.company,
            website: this.state.website,
            location: this.state.location,
            status: this.state.status,
            skills: this.state.skills,
            githubusername: this.state.githubusername,
            bio: this.state.bio,
            wechat: this.state.wechat,
            QQ: this.state.QQ,
            tengxunkt: this.state.tengxunkt,
            wangyikt: this.state.wangyikt,
        }
        // console.log(profileData)
        this.props.createProfile(profileData, this.props.history)
    }
    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    render() {
        const { errors, displaySocialInputs } = this.state;

        let socialInputs;

        const options = [
            { label: "*", value: '*' },
            { label: 'Junior Developer', value: '' },
            { label: 'Senior Developer', value: '' },
            { label: 'HighDeveloper', value: '' },
            { label: 'Manager', value: '' },
            { label: 'backend Developer', value: '' },
            { label: 'python machine learning', value: 'Python' },
            { label: 'Other', value: '' }
        ];

        if (displaySocialInputs) {
            socialInputs = (
                <div>
                    <InputGroup
                        placeholder=""
                        name="wechat"
                        icon="fab fa-weixin"
                        value={this.state.wechat}
                        onChange={this.onChange}
                        error={errors.wechat}
                    />

                    <InputGroup
                        placeholder="QQ"
                        name="QQ"
                        icon="fab fa-qq"
                        value={this.state.QQ}
                        onChange={this.onChange}
                        error={errors.QQ}
                    />

                    <InputGroup
                        placeholder=""
                        name="tengxunkt"
                        icon="fab fa-wechat"
                        value={this.state.tengxunkt}
                        onChange={this.onChange}
                        error={errors.tengxunkt}
                    />

                    <InputGroup
                        placeholder=""
                        name="wangyikt"
                        icon="fab fa-wechat"
                        value={this.state.wangyikt}
                        onChange={this.onChange}
                        error={errors.wangyikt}
                    />
                </div>
            )
        }

        return (
            <div className="create-profile">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="display-4 text-center"></h1>
                            <p className="lead text-center">,!</p>
                            <small className="d-block pb-3">*</small>

                            <form onSubmit={this.onSubmit}>
                                <TextFieldGroup
                                    placeholder="* Profile Handle"
                                    name="handle"
                                    value={this.state.handle}
                                    onChange={this.onChange}
                                    error={errors.handle}
                                    info="handle, email"
                                />

                                <SelectListGroup
                                    placeholder="Status"
                                    name="status"
                                    value={this.state.status}
                                    onChange={this.onChange}
                                    options={options}
                                    error={errors.status}
                                    info=""
                                />

                                <TextFieldGroup
                                    placeholder=""
                                    name="company"
                                    value={this.state.company}
                                    onChange={this.onChange}
                                    error={errors.company}
                                    info=""
                                />

                                <TextFieldGroup
                                    placeholder=""
                                    name="website"
                                    value={this.state.website}
                                    onChange={this.onChange}
                                    error={errors.website}
                                    info=""
                                />
                                <TextFieldGroup
                                    placeholder=""
                                    name="location"
                                    value={this.state.location}
                                    onChange={this.onChange}
                                    error={errors.location}
                                    info=" (. )"
                                />
                                <TextFieldGroup
                                    placeholder="* "
                                    name="skills"
                                    value={this.state.skills}
                                    onChange={this.onChange}
                                    error={errors.skills}
                                    info=" (: HTML,CSS,JavaScript,PHP)"
                                />
                                <TextFieldGroup
                                    placeholder="Github "
                                    name="githubusername"
                                    value={this.state.githubusername}
                                    onChange={this.onChange}
                                    error={errors.githubusername}
                                    info=", github"
                                />
                                <TextAreaFieldGroup
                                    placeholder=""
                                    name="bio"
                                    value={this.state.bio}
                                    onChange={this.onChange}
                                    error={errors.bio}
                                    info=""
                                />

                                <div className="mb-3">
                                    <button className="btn btn-light" type="button" onClick={() => {
                                        this.setState(prevState => ({
                                            displaySocialInputs: !prevState.displaySocialInputs
                                        }))
                                    }}>
                                        
                                    </button>
                                    <span className="text-muted"></span>
                                    {socialInputs}
                                </div>
                                <input type="submit" value="" className="btn btn-info btn-block mt-4" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

CreateProfile.propTypes = {
    createProfile: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    profile: state.profile,
    errors: state.errors
})

export default connect(mapStateToProps, { createProfile })(withRouter(CreateProfile))
