import React from 'react'
import PropTypes from 'prop-types'


const ProfileCreds = (props) => {
    const { experience, education } = props;

    const expItems = experience.map(exp => (
        <li key={exp._id} className="list-group-item">
            <h4>{exp.company}</h4>
            <p>
                {exp.from}  {exp.to === '' ? '' : exp.to}
            </p>
            <p>
                <strong>:</strong> {exp.title}
            </p>
            <p>
                {exp.location === '' ? null : (
                    <span>
                        <strong>: </strong> {exp.location}
                    </span>
                )}
            </p>
            <p>
                {exp.description === '' ? null : (
                    <span>
                        <strong>: </strong> {exp.description}
                    </span>
                )}
            </p>
        </li>
    ));

    const eduItems = education.map(edu => (
        <li key={edu._id} className="list-group-item">
            <h4>{edu.school}</h4>
            <p>
                {edu.from}  {edu.to === '' ? '' : edu.to}
            </p>
            <p>
                <strong>:</strong> {edu.degree}
            </p>
            <p>
                <strong>:</strong> {edu.fieldofstudy}
            </p>
            <p>
                {edu.description === '' ? null : (
                    <span>
                        <strong>: </strong> {edu.description}
                    </span>
                )}
            </p>
        </li>
    ));

    return (
        <div className="row">
            <div className="col-md-6">
                <h3 className="text-center text-info"></h3>
                {expItems.length > 0 ? (
                    <ul className="list-group">{expItems}</ul>
                ) : (
                        <p className="text-center"></p>
                    )}
            </div>

            <div className="col-md-6">
                <h3 className="text-center text-info"></h3>
                {eduItems.length > 0 ? (
                    <ul className="list-group">{eduItems}</ul>
                ) : (
                        <p className="text-center"></p>
                    )}
            </div>
        </div>
    )

}
ProfileCreds.propTypes = {
    experience: PropTypes.array.isRequired,
    education: PropTypes.array.isRequired,
}
export default ProfileCreds;